<template>
  <div class="line flex flex-direction" v-cloak v-if="loaded">
    <navBar :title="$route.meta.title" left-arrow @click-left="$router.back()" class="font-regular"></navBar>

    <panel type="time" title="">
      <div class="pbody">
        <span class="number">{{create.year}}</span>
        <span class="padding-xs"></span>
        <span class="number">{{create.month}}</span>
        <span class="padding-xs"></span>
        <span class="number">{{create.date}}</span>
        <span class="padding-xs"></span>
      </div>
    </panel>

    <panel type="computer" title="">
      <div class="pbody">{{data.terminal_construction}}</div>
    </panel>

    <panel type="rmb" title="" label="()">
      <div class="pbody">
        <span class="number">{{sale.month}}</span>
        <span class="padding-xs"></span>
      </div>
    </panel>

    <panel type="chart" title="" label="()">
      <ve-histogram
        :data="sale"
        :settings="chart.settings"
        :legend-visible="false"
        :grid="chart.grid"
        height="180px"
      ></ve-histogram>
    </panel>

    <panel type="box" title="">
      <div class="pbody">{{data.business_product}}</div>
    </panel>

    <panel type="memory" title="">
      <div class="pbody">{{data.business_scope}}</div>
    </panel>

    <panel type="attack" title="">
      <div class="pbody">
        <competitor
          v-for="competitor in data.competitors"
          :key="competitor.name"
          :name="competitor.name"
          :sale="competitor.money"
          :info="competitor.introduce"
        />
      </div>
    </panel>
  </div>
</template>

<script>
import { NavBar } from "vant";
import Panel from "@/components/panel";
import Competitor from "@/components/competitor";
import VeHistogram from "v-charts/lib/histogram.common";

export default {
  data: function() {
    return {
      data: {},
      loaded: false,
      chart: {
        settings: {
          labelMap: {
            money: ""
          },
          // yAxisType: ["KMB"],
          itemStyle: {
            color: "#686cff"
          },
          digit: 1
        },
        grid: {
          top: 20,
          bottom: 10
        }
      },
      sale: {
        columns: ["date", "money"],
        rows: []
      },
      create: {}
    };
  },
  components: {
    NavBar,
    Panel,
    Competitor,
    VeHistogram
  },
  async beforeMount() {},
  async created() {
    const id = this.$route.params.id;
    await this.$http.get(`api/store/${id}`).then(data => {
      if (data.competitors) {
        data.competitors = JSON.parse(data.competitors) || [];
      }

      this.data = data;

      let create = new Date(data.establishment_time * 1000);
      this.create = {
        year: create.getFullYear(),
        month: create.getMonth() + 1,
        date: create.getDate()
      };
    });

    await this.$http.get(`api/store/${id}/income`).then(data => {
      this.sale.rows = data;
      this.sale.month = (data[data.length - 1].money).toLocaleString('en-US');
    });

    this.loaded = true;
  }
};
</script>

<style scoped>
.line {
  height: 100%;
  background: #ededee;
}

.van-nav-bar {
  line-height: 48px;
  background: #34569b;
}

.van-nav-bar__title {
  color: #fff;
}

.van-nav-bar .van-icon {
  color: #fff;
}

[v-cloak] {
  display: none;
}

.number {
  font-size: 48px;
  font-weight: bold;
  color: #34579b;
}

.pbody {
  padding-left: 40px;
}
</style>