<template>
  <div class="user" v-cloak>
    <navBar :title="$route.meta.title" left-arrow @click-left="$router.back()" class="font-regular"></navBar>

    <cell-group class="font-regular">
      <field label="" v-model="user.name" input-align="right" placeholder="" required/>
      <field
        label=""
        v-model="user.phone"
        input-align="right"
        placeholder=""
        required
        readonly
      />
      <field label="" v-model="user.address" input-align="right" placeholder="" required/>
      <field
        label=""
        v-model="user.email"
        input-align="right"
        placeholder=""
        type="email"
        required
      />
      <field
        label=""
        v-model="user.role.name"
        input-align="right"
        placeholder=""
        required
        readonly
        @click="showPicker = true"
      />

      <!-- <cell
        title=""
        is-link
        :value="user.name || '()'"
        :to="{path: 'input', query: {type: 'text', title: '', store: 'user', key: 'name'}}"
      />-->
      <!-- <cell title="" is-link :value="user.phone"/>

      <cell
        title=""
        is-link
        :value="user.address || '()'"
        :to="{path: 'input', query: {type: 'text', title: '', store: 'user', key: 'address'}}"
      />

      <cell
        title=""
        is-link
        :value="user.email || '()'"
        :to="{path: 'input', query: {type: 'text', title: '', store: 'user', key: 'email'}}"
      />

      <cell title="" is-link :value="user.role || '()'" @click="showPicker = true"/>-->
    </cell-group>

    <popup v-model="showPicker" position="bottom">
      <picker
        :columns="roles"
        show-toolbar
        title=""
        value-key="name"
        @confirm="onShowPicker"
        @cancel="showPicker = false"
        :default-index="index"
      />
    </popup>

    <div class="button-group flex flex-direction margin-xl font-regular">
      <button class="btn text-sm bg-white" :class="{'bg-blue': check}" @click="onSubmit"></button>
    </div>
  </div>
</template>

<style scoped>
.van-icon {
  color: #333;
}

.btn {
  height: 80px;
  width: 100%;
  border: none;
  border-radius: 5px;
  border: 1px solid #f0f0f0;
}
</style>

<script>
import { NavBar, CellGroup, Picker, Popup, Field } from "vant";
import http from "@/utils/http";

export default {
  data() {
    return {
      roles: [],
      showPicker: false
    };
  },
  computed: {
    index() {
      return (
        this.roles.findIndex(role => {
          role.name === this.user.role.name;
        }) || 0
      );
    },
    check() {
      return (
        this.user.name.length &&
        this.user.address.length &&
        this.user.email.length &&
        this.user.role_id
      );
    },
    user() {
      return this.$root.user;
    }
  },
  components: {
    NavBar,
    CellGroup,
    Picker,
    Popup,
    Field
  },
  methods: {
    onShowPicker(data) {
      this.$root.user.role.name = data.name;
      this.$root.user.role_id = data.id;

      this.showPicker = false;
    },
    onSubmit() {
      if (!this.check) return;

      const data = {
        phone: this.user.phone,
        email: this.user.email,
        address: this.user.address,
        name: this.user.name,
        role_id: this.user.role_id
      };

      http.put("api/user/audit", data).then(() => {
        this.$root.user.audit_status = 3;
        this.$router.replace(`/user`);
      });
    }
  },
  async created() {
    await http.get("api/role").then(data => {
      this.roles = data;
    });
  }
};
</script>