<template>
  <div class="user" v-cloak>
    <navBar :title="title" :left-arrow="goBack" @click-left="$router.back()" class="font-regular"></navBar>

    <div v-if="user.audit_status == 1">
      <cell-group class="font-regular margin-tb-xs">
        <cell title="" center style="line-height: 0">
          <uploader :after-read="onUpload">
            <img width="100" height="100" :src="user.headimg || avatar" class="img" />
          </uploader>
        </cell>
      </cell-group>

      <!-- <cell-group class="font-regular margin-tb-xs">
        <cell title="" is-link :to="{name: 'stores', query: {module_id: 1, title: ''}}" v-if="user.roles.market.market" />
        <cell title="" is-link :to="{name: 'stores', query: {module_id: 21, title: ''}}" v-if="user.roles.store.CHINTStore" />
      </cell-group> -->

      <cell-group class="font-regular margin-tb-xs" v-if="user.role.modules.length">
        <cell title="" is-link :to="{name: 'operation'}" />
      </cell-group>

      <cell-group class="font-regular margin-tb-xs" v-if="user.roles.dev.devStatistics">
        <cell title="" is-link :to="{name: 'develop'}" />
      </cell-group>

      <cell-group class="font-regular margin-tb-xs padding">
        <div class="title padding-bottom-xs solid-bottom"></div>
        <div class="text-df padding-top-xs">{{user.name}}</div>
        <div class="text-df padding-top-xs">{{user.phone}}</div>
        <div class="text-df padding-top-xs">{{user.email}}</div>
        <div class="text-df padding-top-xs">{{user.address}}</div>
        <div class="text-df padding-top-xs">{{user.role.name}}</div>
      </cell-group>

      <div class="btn bg-blue" @click="logout"></div>
    </div>

    <div class="error text-center padding-top-xl" v-else-if="user.audit_status == 3">
      <icon name="todo-list-o" size="8rem" color="#c0c0c2" />
      <div></div>
    </div>

    <logo />
  </div>
</template>

<style scoped>
.van-nav-bar {
  line-height: 47px;
}

.van-icon {
  color: #333;
}

.btn {
  text-align: center;
  padding: 20px;
  margin: 20px;
  border: none;
  border-radius: 5px;
}

.img {
  border-radius: 100%;
}

.title {
  color: #3672e7;
}

.error {
  margin: 0 auto;
}
</style>

<script>
import logo from "@/components/logo";
import { NavBar, CellGroup, Cell, Uploader, Icon } from "vant";
import avatar from "@/assets/avatar.png";

export default {
  data() {
    return {
      goBack: false,
      modules: [],
      avatar
    };
  },
  computed: {
    user() {
      return this.$root.user;
    },
    title() {
      return this.user.audit_status == 3 ? "" : "";
    }
  },
  components: {
    NavBar,
    // Field,
    CellGroup,
    Cell,
    logo,
    Uploader,
    Icon
  },
  methods: {
    logout() {
      localStorage.removeItem("token");
      this.$router.replace("/login");
    },
    onUpload(data) {
      this.value = data.content;
      const name =
        Date.parse(new Date()) +
        Math.random()
          .toString()
          .slice(-6) +
        "." +
        data.file.name.split(".").pop();

      this.$http
        .file("api/user/avatar", { file: data.file, name })
        .then(url => {
          this.user.headimg = url;
        });
    }
  },
  async created() {
    await this.$http.get("/api/user").then(data => {
      if (data.role == null) {
        data.role = { name: "" };
      }

      this.$root.user = data;

      if (data.audit_status == 1) this.goBack = true;
      if (data.audit_status == 0 || data.audit_status == 2)
        this.$router.replace("/audit");
    });
  }
};
</script>
